.product-management {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .content-product-management {
    position: relative;
    background-color: white;
    height: 100vh;
    .header-category-management {
      display: flex;
      justify-content: space-between;
      padding: 0 15px;
      position: relative;
      height: max-content;
      // border-bottom: 1px solid #e9e8e8;
      .header-category-management-left {
        padding-top: 8px;
        font-weight: bold;
        font-size: 1.2rem;
        span {
          padding: 15px 0;
          // position: absolute;
          // top: 50%;
          // width: max-content;
          vertical-align: sub;
        }
      }
      .header-category-management-right {
        position: relative;
        .add-category {
          padding: 6px 3px;
          padding-left: 20px;
          display: flex;
          margin-top: 8px;
          background-color: #0439d8;
          border-radius: 20px;
          color: white;
          text-decoration: none;
          span.title-add {
            margin-right: 8px;
            margin-top: 2px;
          }
          .icon-add {
            padding-left: 10px;
            position: relative;
            height: 15px;
            width: 15px;
            background-color: white;
            justify-content: center;
            border-radius: 50%;
            padding: 15px;

            .icon_plus {
              height: 15px;
              width: 15px;
              position: absolute;
              top: 52%;
              left: 58%;
              transform: translate(-50%, -50%);
              color: #71abff;
            }
          }
        }
      }
    }
    .line-header {
      margin-top: 0.5rem;
    }
    .caterory-products {
      .title-caterory-products {
        display: flex;
        justify-content: space-between;
        padding: 15px 15px;
        position: relative;
        border-bottom: 1px solid #e6e6e8;
        .item-title-caterory {
          color: #999999;
        }
        .item-title-caterory.img-product {
          position: absolute;
          left: 10%;
        }
        .item-title-caterory.name-product {
          position: absolute;
          left: 20%;
        }
        .item-title-caterory.unit-price {
          position: absolute;
          left: 55%;
        }
        .item-title-caterory.amount-product {
          position: absolute;
          left: 65%;
        }
        .item-title-caterory.statusproduct {
          position: absolute;
          left: 78%;
        }
      }
    }
    .list-category {
      .box-category {
        .content-box-category {
          display: flex;
          position: relative;
          justify-content: space-between;
          padding: 5px 15px;
          border-bottom: 1px solid #e6e6e8;
          .item-detail-caterory.sku {
            left: 0px;
            width: 80px;
            height: 40px;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          .item-detail-caterory {
            padding: 12px 0;
            .icon {
              margin-right: 8px;
              position: relative;
              height: 20px;
              width: 20px;
              border: 1px solid #71abff;
              justify-content: center;
              border-radius: 50%;
              padding: 10px;
              .fa.fa-edit {
                color: #71abff;
                font-size: 18px;
              }
              // .fa-trash:before {
              //   height: 20px;
              //   width: 20px;
              //   position: absolute;
              //   top: 50%;
              //   left: 50%;
              //   transform: translate(-50%, -50%);
              // }
            }
          }
          .item-detail-caterory:last-child {
            a {
              margin-right: 5px;
            }
          }
          .img-product {
            position: absolute;
            left: 12%;
            height: 35px;
            top: 50%;
            transform: translate(-50%, -50%);
          }
          .item-detail-caterory.name-product {
            position: absolute;
            left: 20%;
          }

          .item-detail-caterory.unit-price {
            position: absolute;
            left: 55%;
          }
          .item-detail-caterory.amount-product {
            position: absolute;
            left: 65%;
          }
          .item-detail-caterory.statusproduct {
            position: absolute;
            left: 78%;
          }
        }
      }
    }
  }
}
