.page-order {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .page-content-order {
    padding: 15px;
    position: relative;
    .header-order {
      display: flex;
      justify-content: space-between;
      .header-order-left {
        font-weight: bold;
        font-size: 1.2rem;
        span {
          vertical-align: sub;
        }
      }
      .header-order-right {
        display: flex;
        justify-content: flex-start;
        .box-search {
          border: 1px solid #e5e5e5;
          border-radius: 30px;
          .input {
            .img-email {
              left: 8%;
            }
            .field {
              width: 250px;
              padding-left: 38px;
            }
          }
        }
        .btn-img-list-product {
          width: 60px;
          position: relative;
          .img-chose-list {
            height: 35px;
            position: absolute;
            top: 50%;
            right: 0px;
            transform: translate(-50%, -50%);
          }
        }
        .btn-cart {
          display: flex;
          position: relative;
          padding: 0 10px;
          background-color: #f25366;
          border-radius: 30px;
          padding-left: 50px;
          button {
            padding-right: 5px;
            padding-left: 0px;
            color: white;
            background: transparent;
            border: none;
          }
          .box-number-amount {
            position: relative;
            margin-top: 9%;
            height: 20px;
            width: 20px;
            background-color: white;
            justify-content: center;
            border-radius: 50%;
            padding: 15px;
            .number-amount {
              height: 20px;
              width: 20px;
              position: absolute;
              top: 42%;
              left: 65%;
              transform: translate(-50%, -50%);
              color: black;
            }
          }
          .img-cart {
            height: 30px;
            position: absolute;
            top: 50%;
            left: 17%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
    .line-header {
      margin-top: 0px;
    }
  }
  .list-product {
    .caterory-name {
      position: relative;
      .name {
        padding-left: 15px;
        font-weight: bold;
      }
      hr {
        position: absolute;
        top: -7%;
        left: 17%;
        /* transform: translate(-50%, -50%); */
        height: 1px;
        width: 83%;
      }
    }
    .product {
      width: auto;
      height: 200px;
      text-align: center;
      position: relative;
      .img-product {
        width: 100px;
        position: absolute;
        left: 50%;
        top: 32%;
        transform: translate(-50%, -50%);
      }
      .name-product {
        position: absolute;
        left: 50%;
        top: 60%;
        width: 100%;
        font-size: 1.1rem;
        transform: translate(-50%, -50%);
      }
      .price-product {
        position: absolute;
        left: 50%;
        top: 71%;
        width: 100%;
        color: #0439d8;
        font-weight: bold;
        font-size: 1rem;
        transform: translate(-50%, -50%);
      }
      .btn-add {
        width: 45%;
        position: absolute;
        left: 50%;
        top: 86%;
        // width: 100%;
        transform: translate(-50%, -50%);
        border: 1px solid #f25366;
        color: #f25366;
        padding: 2px 12px;
        i {
          padding-right: 5px;
        }
      }
    }
    // .product {
    //   width: auto;
    //   height: 200px;
    //   position: relative;
    //   .img-product {
    //     position: absolute;
    //     top: 28%;
    //     left: 50%;
    //     transform: translate(-50%, -50%);
    //   }
    //   .name-product {
    //     position: fixed;
    //     top: 56%;
    //     /* left: 50%; */
    //     transform: translate(-50%, -50%);
    //   }
    // }
    .slick-list {
      padding: 0 40px;
    }
    .slick-slider {
      .slick-prev {
        padding: 0 40px;
      }
      .slick-next {
        padding: 0 65px;
      }
      .slick-next::before,
      .slick-prev::before {
        color: #d9e4ed;
        font-size: 30px;
        line-height: 1;
        opacity: 0.75;
      }
      .slick-arrow.slick-prev {
        z-index: 99999999;
      }
    }
  }
}
@media (max-width: 1240px) {
  .page-order .list-product .caterory-name hr {
    width: 75%;
  }
}
