.transaction_successful {
  position: relative;
  width: 100%;
  height: 100%;

  .contain_transaction_successful {
    padding: 5% 10%;
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .img_success {
      width: 100px;
    }
    .txt_success {
      margin-top: 1rem;
      font-size: 1.3rem;
      font-weight: bold;
    }
    .box_info {
      .content-box-info {
        position: absolute;
        top: 68%;
        left: 50%;
        transform: translate(-50%, -50%);
        min-width: 450px;
        border: 1px solid #e5e5e5;
        border-radius: 8px;
        .item_box_info {
          border-bottom: 1px solid #e5e5e5;
          .content_item_box_info {
            display: flex;
            justify-content: space-between;
            padding: 10px 15px;
            .amount_total {
              font-size: 1.2rem;
              color: #f25366;
              font-weight: bold;
            }
          }
        }
        .item_box_info:last-child {
          border-bottom: none;
        }
      }
    }
    .btn_come_back_home {
      position: absolute;
      top: 92%;
      left: 50%;
      width: 20%;
      transform: translate(-50%, -50%);
      padding: 12px;
      margin-top: 20px;
      color: white;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: bold;
      border: none;
      border-radius: 8px;
      background: linear-gradient(to right, #70a9ff, #1b21a0);
      background: -moz-linear-gradient(right, #70a9ff, #1b21a0);
      background: -webkit-linear-gradient(right, #70a9ff, #1b21a0);
      background: -o-linear-gradient(right, #70a9ff, #1b21a0);
    }
  }
}
