.signin {
  background-image: url(../../../../assets/icons/background.svg);
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1001;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.content-signin {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form-login {
  padding-left: 0px;
  text-align: unset;
  min-height: 400px;
}

.viewLogo {
  text-align: center;
  padding-bottom: 20px;
  .logo-ecpay {
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 45%;
  }
}
.box-login {
  padding: 0;
  border: 1.5px solid #e9e8e8;
  border-radius: 8px;
  min-width: 280px;
  li {
    list-style-type: none;
    border-bottom: 1.5px solid #e9e8e8;
  }
  li:last-child {
    border-bottom: none;
  }
}
.box-login.input_dropdown {
  padding: 0;
  border: 1.5px solid #e9e8e8;
  border-radius: 8px;
  min-width: 280px;
  li {
    display: flex;
    position: relative;
    list-style-type: none;
    border-bottom: 1.5px solid #e9e8e8;
    img {
      position: absolute;
      top: 58%;
      left: 7%;
      transform: translate(-50%, -50%);
      height: 20px;
      width: 20px;
    }
    .MuiFormControl-marginNormal {
      padding-left: 35px;
    }
    .MuiInput-underline:before {
      border-bottom: none;
    }
    .MuiSelect-select {
      padding-top: 0px !important;
      padding: 6px 0;
      padding-right: 24px;
    }
    .MuiInputLabel-formControl {
      left: 35px;
    }
  }
  li:last-child {
    border-bottom: none;
    img {
      position: absolute;
      top: 50%;
      left: 7%;
      transform: translate(-50%, -50%);
      height: 20px;
      width: 20px;
    }
    .input {
      width: 100%;
    }
  }
}
.btn-login {
  width: 100%;
  padding: 12px;
  margin-top: 20px;
  color: white;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 8px;
  background: linear-gradient(to top, #6da7fe, #2969ea);
  background: -moz-linear-gradient(top, #6da7fe, #2969ea);
  background: -webkit-linear-gradient(top, #6da7fe, #2969ea);
  background: -o-linear-gradient(top, #6da7fe, #2969ea);
}
.btn-login[disabled] {
  color: #777777;
  border: none;
  background: linear-gradient(to top, #fff, #fff);
  background: -moz-linear-gradient(top, #fff, #fff);
  background: -webkit-linear-gradient(top, #fff, #fff);
  background: -o-linear-gradient(top, #fff, #fff);
}
.nav-tabs-login {
  border-bottom: 0px;
  background-color: white;
  border-radius: 30px;
}
.login-tabs {
  width: 50%;
  text-align: center;
  .tab-link.active {
    color: #0439d8 !important;
    font-weight: bold;
    border: 1px solid #71abff;
    border-radius: 30px;
  }
  .nav-link.tab-link {
    color: #71abff;
    width: 140px;
  }
}
.nav-tabs-login {
  border-bottom: none;
  margin-bottom: 30px;
}
.box_otp {
  position: absolute;
  padding-top: 20px;
  text-align: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  p {
    color: #999999;
  }

  .btn_resent_otp {
    position: absolute;
    background: transparent;
    left: 50%;
    top: 120%;
    transform: translate(-50%, -50%);
    contain: 'sadsadas';
    border: none;
    text-decoration: none;
  }
  input {
    width: 60px !important;
    height: 80px !important;
    text-align: center !important;
    margin-right: 20px !important;
    border-radius: 8px;
    border: 2px solid #dddddd;
  }
}
.yd-rememberme-validation-showpass {
  text-align: center;
}
