.content-pop-up {
  position: relative;
  min-width: 800px;
  .off-pop-up {
    position: absolute;
    top: 0px;
    right: 0px;
  }
  .viewClose {
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 90000000;
  }
  .icon-close {
    position: relative;
    height: 20px;
    width: 20px;
    // border: 1px solid #71abff;
    justify-content: center;
    border-radius: 50%;
    padding: 15px;
    background-color: #f25366;
    img {
      height: 20px;
      width: 20px;
      position: absolute;
      top: 50%;
      left: 51%;
      transform: translate(-50%, -50%);
    }
  }
  .pop_up_pay {
    display: flex;
    justify-content: space-between;
    .detail-order-pop-up {
      width: 70%;
      padding: 15px;
      .name-order-popup {
        padding-bottom: 5px;
        display: flex;
        justify-content: space-between;
        span {
          font-weight: bold;
          font-size: 1.2rem;
        }
        .status-order {
          font-weight: unset;
          padding: 1px 12px;
          background-color: #f2b140;
          border-radius: 8px;
          color: white;
          font-size: 0.9rem;
        }
      }
      .name-column,
      .item-product-popup {
        padding: 3px 0px;
        display: flex;
        justify-content: space-between;
        // border-bottom: 1px solid #d5d5d5;
        color: #666666;
        font-size: 0.9rem;
        .name-product-pop-up {
          width: 39%;
        }
      }
      .item-product-popup {
        .name-product-pop-up,
        .number-product-popup,
        .amount-product-popup {
          color: black;
        }
      }
      .line-name-column {
        margin: 3px;
        color: #e5e5e5;
        height: 3px;
      }
      .action-order {
        margin-top: 10px;
        padding-left: 0px;
        text-align: center;
        .item {
          margin-right: 20px;
          list-style-type: none;
          display: inline-block;
          a {
            padding: 8px 30px;
            border: 1px solid #407bfd;
            color: #407bfd;
            border-radius: 8px;
            text-transform: uppercase;
            font-weight: bold;
          }
        }
        .item:last-child {
          a {
            border: 1px solid #f25366;
            color: #f25366;
          }
        }
      }
    }
    .qr-code-pop-up {
      background-color: #f8f8f8;
      width: 40%;
      padding-top: 5.5%;
      text-align: center;
      color: #666666;
      .icon-qrcode {
        width: 200px;
      }
    }
  }
}
