.category-management {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .content-category-management {
    position: relative;
    background-color: white;
    height: 100vh;
    .header-category-management {
      display: flex;
      justify-content: space-between;
      padding: 0 15px;
      position: relative;
      height: max-content;
      // border-bottom: 1px solid #e9e8e8;
      .header-category-management-left {
        padding-top: 8px;
        font-weight: bold;
        font-size: 1.2rem;
        span {
          padding: 15px 0;
          // position: absolute;
          // top: 50%;
          // width: max-content;
          vertical-align: sub;
        }
      }
      .header-category-management-right {
        position: relative;
        .add-category {
          display: flex;
          margin-top: 8px;
          background-color: #0439d8;
          border-radius: 20px;
          color: white;
          span.title-add {
            margin-right: 8px;
            margin-top: 2px;
          }
          .icon-add {
            padding-left: 10px;
            position: relative;
            height: 15px;
            width: 15px;
            background-color: white;
            justify-content: center;
            border-radius: 50%;
            padding: 15px;

            .icon_plus {
              height: 15px;
              width: 15px;
              position: absolute;
              top: 52%;
              left: 52%;
              transform: translate(-50%, -50%);
              color: #71abff;
            }
          }
        }
      }
    }
    .line-header {
      margin-top: 0.5rem;
    }
    .caterory-products {
      .title-caterory-products {
        display: flex;
        justify-content: space-between;
        padding: 15px 15px;
        position: relative;
        .item-title-caterory {
          color: #999999;
        }
        .item-title-caterory.name-caterory {
          position: absolute;
          left: 15%;
        }
        .item-title-caterory.description-category {
          position: absolute;
          left: 35%;
        }
        .item-title-caterory.created-time {
          position: absolute;
          right: 18%;
        }
      }
    }
    .list-category {
      .box-category {
        .content-box-category {
          display: flex;
          position: relative;
          justify-content: space-between;
          padding: 0px 15px;
          border-bottom: 1px solid #e6e6e8;
          .item-detail-caterory {
            padding: 15px;
          }
          .item-detail-caterory.name-caterory {
            position: absolute;
            left: 15%;
          }
          .item-detail-caterory.description-category {
            position: absolute;
            left: 35%;
          }
          .item-detail-caterory.created-time {
            position: absolute;
            right: 18%;
          }
          .item-detail-caterory:last-child {
            padding: 12px;
            a {
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
}
