.page-waiting-pay {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .content-wating-pay {
    position: relative;
    .title-wating-pay {
      padding: 15px;
      span {
        font-weight: bold;
        font-size: 1.2rem;
      }
    }
    .nav-tabs-wating-pay {
      position: absolute;
      top: 22px;
      right: 0px;
      border-bottom: 0px;
      background-color: transparent;
      border-radius: 30px;
      width: 32%;
    }
    .wating-pay-tabs {
      width: 50%;
      text-align: center;
      .tab-link.active.show {
        padding-bottom: 0px;
        color: #0439d8 !important;
        border-bottom: 2px solid #71abff;
      }
      .nav-link.tab-link {
        color: #666666;
        border: none;
        background-color: transparent;
      }
    }
  }
}
.list-order-waiting {
  padding: 0 15px;
  .title-litst-order,
  .detail-litst-order {
    display: flex;
    justify-content: space-between;
    color: #999999;
  }
  .title-litst-order {
    position: relative;
    padding: 10px 0px;
    border-bottom: 2px solid #e5e5e8;
    .title-amount {
      position: absolute;
      top: 50%;
      left: 49%;
      transform: translate(-50%, -50%);
    }
    .title-time {
      margin-right: 60px;
    }
  }
  .detail-litst-order {
    display: flex;
    position: relative;
    justify-content: space-between;
    padding: 15px 0px;
    border-bottom: 1px solid #e6e6e8;
    .order-code {
      color: #407afc;
      font-weight: bold;
    }
    .order-amount {
      position: absolute;
      left: 46%;
      color: black;
    }
    .order-time {
      margin-right: 40px;
    }
    .icon-trash {
      position: absolute;
      right: 0px;
      i {
        color: #f25366;
        font-size: 1rem;
      }
    }
  }
}
