.air_ticket {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_air_ticket {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .box_action {
    padding: 10%;
    text-align: center;
    display: flex;
    justify-content: center;
    .item_box {
      margin-right: 5%;
      text-decoration: none;
      img {
        height: 80px;
      }
      p {
        margin-top: 0.5rem;
        color: black;
        font-size: 1.1rem;
      }
    }
  }
}
