.change_password,
.change_pin_code {
  position: relative;
  background-color: white;
  margin: 15px 0;
  margin-top: 20px;
  .title_change_password {
    text-transform: uppercase;
    .line_title {
      padding: 15px 0px;
      span {
        margin: 15px;
        font-weight: bold;
        font-size: 1.1rem;
      }
    }
    .line-header {
      margin: 0px;
    }
  }
  .box_chage_pass {
    padding: 15px;
    width: 50%;
    .box-input {
      position: relative;
      padding-top: 25px;
      .title-product-code {
        color: #666666;
        padding-bottom: 10px;
      }
    }
    .card-cover {
      border: 2px solid #e2e2e2;
      border-radius: 8px;
      .field {
        padding-left: 12px !important;
      }
    }
    .box_chage_pass {
      padding: 15px;
    }
    .group-btn-pop-up {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .btn-pop-up {
        margin-right: 10px;
        width: 50%;
        padding: 12px;
        margin-top: 20px;
        color: white;
        font-size: 14px;
        font-weight: bold;
        border-radius: 8px;
        background: linear-gradient(to right, #70a9ff, #1b22a1);
        background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
        background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
        background: -o-linear-gradient(right, #70a9ff, #1b22a1);
      }
      .btn-pop-up:last-child {
        color: #777777;
        background: linear-gradient(to right, #fff, #fff);
        background: -moz-linear-gradient(right, #fff, #fff);
        background: -webkit-linear-gradient(right, #fff, #fff);
        background: -o-linear-gradient(right, #fff, #fff);
      }
    }
  }
}
