.bill_electric {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_bill_electric {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .title-provider {
    font-weight: bold;
    padding: 0 8%;
  }
  .list_provider {
    display: flex;
    justify-content: space-between;
    padding: 15px 8%;
    .item-bill {
      display: inline-block;
      border: 2px solid #d2d6d8;
      padding: 15px;
      margin-right: 15px;
      margin-bottom: 15px;
      width: 130px;
      background-color: white;
      border-radius: 5px;
      text-align: center;
      .icon-service {
        height: 40px;
      }
      p {
        display: block;
        // display: -webkit-box;
        margin: 3px auto;
        font-size: 1rem;
        line-height: 1.4;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 0px;
        color: #2d2d2d;
      }
    }
    .item-bill:hover,
    .item-bill.active {
      border: 2px solid #71abff;
      background-color: white;
      color: #fff;
      text-decoration: none;
      i {
        color: #ffff;
      }
      p {
        color: #2fa933;
      }
    }
    .item-bill:hover a p {
      text-decoration: none;
    }
  }
  form {
    margin: 0px 8%;
    .card-cover {
      margin-right: 40%;
      margin-top: 20px;
      margin-bottom: 10px;
      border: 2px solid #e2e2e2;
      border-radius: 8px;
      .field {
        padding-left: 12px !important;
      }
    }
    .text-guide {
      margin-top: 10px;
      text-decoration: none;
    }
    p {
      .MuiCheckbox-root {
        color: #d2d6d8;
      }
      .MuiCheckbox-colorPrimary.Mui-checked {
        color: #0439d8;
      }
      .MuiTypography-root.MuiFormControlLabel-label {
        color: #666666;
      }
    }
    .btn-bill-electric {
      padding: 12px;
      width: 20%;
      color: white;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #6da7fe, #1b20a0);
      background: -moz-linear-gradient(right, #6da7fe, #1b20a0);
      background: -webkit-linear-gradient(right, #6da7fe, #1b20a0);
      background: -o-linear-gradient(right, #6da7fe, #1b20a0);
    }
  }
}
