.tuition {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_tuition {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .content_form {
    padding: 0px 5%;
    .title-ssc {
      font-weight: bold;
    }
    .MuiFormGroup-root {
      flex-direction: row;
    }
    .MuiRadio-colorSecondary.Mui-checked {
      color: #006afb;
    }
    .MuiTypography-root {
      color: #999999;
    }
    .MuiFormControlLabel-root {
      margin-right: 5%;
    }
    .card-cover {
      margin-right: 40%;
      margin-top: 20px;
      margin-bottom: 30px;
      border: 2px solid #e2e2e2;
      border-radius: 8px;
      .field {
        padding-left: 12px !important;
      }
    }
    .text-guide {
      margin-top: 10px;
      text-decoration: none;
    }
    .btn-continue {
      padding: 12px;
      width: 20%;
      color: white;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #6da7fe, #1b20a0);
      background: -moz-linear-gradient(right, #6da7fe, #1b20a0);
      background: -webkit-linear-gradient(right, #6da7fe, #1b20a0);
      background: -o-linear-gradient(right, #6da7fe, #1b20a0);
    }
  }
}
