.page-history-order {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header-history-order {
    border-bottom: 1px solid #e9e8e8;
    padding: 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .list-order-waiting {
    padding: 0 15px;
    height: 80vh;
    position: relative;
    .title-litst-order,
    .detail-litst-order {
      display: flex;
      justify-content: space-between;
      color: #999999;
    }
    .title-litst-order {
      position: relative;
      border-bottom: 2px solid #e5e5e8;
      .item-title {
        text-align: center;
      }
    }
    .detail-litst-order {
      display: flex;
      position: relative;
      justify-content: space-between;
      padding: 15px 0px;
      border-bottom: 1px solid #e6e6e8;
      .item-history {
        color: #407afc;
        font-weight: bold;
      }
      .item-history.amount {
        position: absolute;
        left: 22%;
        font-weight: normal;
        color: black;
        // transform: translate(-50%, -50%);
      }
      .item-history.qrcode {
        position: absolute;
        left: 46%;
        font-weight: normal;
        color: black;
      }
      .item-history.status {
        position: absolute;
        left: 70%;
        color: #29d3aa;
        font-weight: normal;
      }

      .item-history:last-child {
        color: #29d3aa;
        font-weight: normal;
      }
    }
    .pagination {
      position: absolute;
      left: 50%;
      margin-top: 30px;
      transform: translate(-50%, -50%);
      li {
        position: relative;
        height: 20px;
        width: 20px;
        // background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        margin: 0 5px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: #407afc;
          text-align: center;
          text-decoration: none;
          transform: translate(-50%, -50%);
        }
      }
      li:first-child,
      li:last-child {
        position: relative;
        height: 20px;
        width: 20px;
        background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: white;
          text-align: center;
          transform: translate(-50%, -50%);
        }
      }
      li:last-child {
        a {
          left: 55%;
        }
      }
      li.disabled {
        background-color: #d9e4ed;
      }
      li.active {
        position: relative;
        height: 20px;
        width: 20px;
        justify-content: center;
        border-radius: 50%;
        border: 1px solid #407afc;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}
