.title_info_flight {
  padding-top: 20px;
  position: relative;
  span {
    background-color: white;
    font-weight: bold;
  }
  .line-header {
    position: absolute;
    top: 35px;
    left: 15%;
    width: 86%;
  }
}
.contain_info_flight {
  padding-top: 20px;
  display: flex;
  justify-content: center;

  .item_fligh {
    text-align: center;
    .contain_item_fligh {
      padding: 10px;
      border: 2px solid #eaeaea;
      border-right: none;
      display: flex;
      justify-content: space-between;
      position: relative;
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px;
      img {
        width: 60px;
      }
      .flight_code_ticket_type {
        padding-left: 10px;
        position: relative;
        min-width: 90px;
        height: 100%;
        border-right: 1px solid #eaeaea;

        .contain_flight_code_ticket_type {
          position: absolute;
          top: 50%;
          left: 45%;
          transform: translate(-50%, -50%);
          width: max-content;
          .flight_code {
            font-size: 1.1rem;
            font-weight: bold;
          }
          .ticket_type {
            font-size: 0.9rem;
            color: #999999;
          }
        }
      }
      .flight_time {
        width: max-content;
        position: relative;
        .content_flight_time {
          position: relative;
          display: flex;
          min-width: 250px;
          height: 100%;
          .flight_time_item {
            position: absolute;
            top: 68%;
            height: 100%;
            left: 15%;
            transform: translate(-50%, -50%);
            font-size: 1.3rem;
          }
          img {
            position: absolute;
            top: 50%;
            width: 120px;
            left: 50%;
            transform: translate(-50%, -50%);
          }
          .flight_time_item.last {
            position: absolute;
            top: 34%;
            height: 15px;
            left: 90%;
            transform: translate(-50%, -50%);
            font-size: 1.3rem;
          }
          .flying_time {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
      .flight_time:first-child {
        border-right: none;
      }
    }
    .date_time {
      padding-top: 10px;
      .title_date_time {
        text-transform: uppercase;
        font-weight: bold;
      }
      .last_date_time {
        color: #999;
        text-transform: capitalize;
      }
    }
  }
  .item_fligh:last-child {
    .contain_item_fligh {
      border-left: none;
      border-right: 2px solid #eaeaea;
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px;
      // border-top-left-radius: 12px;
      // border-bottom-left-radius: 12px;
    }
  }
  .img_line_mid {
    height: 85px;
  }
}
