.detail_my_booking {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_detail_my_booking {
    padding: 15px 15px;
    a {
      padding-right: 10px;
      font-size: 1rem;
      vertical-align: bottom;
      .fa-angle-left:before {
        font-size: 1.5rem;
      }
    }
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .title_info_flight {
    width: 100%;
    padding: 15px;
    .line-header {
      top: 29px;
      width: 84%;
    }
  }
  .detail_info_booking {
    padding: 15px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    .info_contant {
      width: 45%;
      .item_info_contant {
        padding: 3px 0;
        display: flex;
        justify-content: space-between;
        .item_info_contant_left {
          color: #999;
        }
      }
      .item_info_contant:first-child {
        .item_info_contant_right {
          text-transform: uppercase;
        }
      }
      .item_info_contant:last-child {
        .item_info_contant_right {
          color: #29d3aa;
        }
      }
    }
    .code_price_booking {
      padding-top: 20px;
      width: 45%;
      display: flex;
      justify-content: center;
      .item_code_price_booking {
        height: 95px;
        width: 220px;
        position: relative;
        border: 2px solid #eaeaea;
        border-radius: 8px;
        padding: 15px 10%;
        .content_item_code_price_booking {
          text-align: center;
          .title_item_code_price_booking {
            color: #999;
          }
          p {
            margin-bottom: 0px;
            font-size: 1.5rem;
            color: #1b26a8;
          }
        }
      }
      .img_line_mid {
        height: 95px;
      }
      .item_code_price_booking:first-child {
        border-right: none;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
      }
      .item_code_price_booking:last-child {
        border-left: none;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
      }
    }
  }
  .detail_passenger {
    position: relative;
    padding: 15px 20px;
    display: flex;
    justify-content: flex-start;

    .item_passenger {
      position: relative;
      margin-right: 20px;
      width: 300px;
      height: 200px;
      display: flex;
      border: 2px solid #d2d6d8;
      padding: 15px;
      margin-bottom: 15px;
      border-radius: 5px;
      text-align: center;
      .title_item_passenger {
        position: absolute;
        top: -12px;
        left: 5px;
        background-color: white;
        padding: 0 7px;
        color: #999;
      }
      .box_info_passenger {
        padding: 5px;
        width: 100%;
        .item_name {
          float: left;
          .name_passenger {
            font-size: 1.1rem;
            text-transform: uppercase;
          }
        }

        .item_box_info_passenger {
          padding: 5px 0px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          .item_box_info_passenger_left {
            color: #999;
            font-size: 0.9rem;
          }
          .item_box_info_passenger_right {
            font-size: 0.9rem;
          }
        }
      }
    }
  }
}
