.store-manager {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .content-store-manager {
    .header-store-manager {
      display: flex;
      justify-content: space-between;
      padding: 0 15px;
      position: relative;
      height: max-content;
      // border-bottom: 1px solid #e9e8e8;
      .header-store-manager-left {
        padding-top: 8px;
        font-weight: bold;
        font-size: 1.2rem;
        span {
          padding: 15px 0;
          // position: absolute;
          // top: 50%;
          // width: max-content;
          vertical-align: sub;
        }
      }
      .header-store-manager-right {
        position: relative;
        .add-store {
          padding: 6px 3px;
          padding-left: 20px;
          display: flex;
          margin-top: 8px;
          background-color: #0439d8;
          border-radius: 20px;
          color: white;
          text-decoration: none;
          span.title-add {
            margin-right: 8px;
            margin-top: 2px;
          }
          .icon-add {
            padding-left: 10px;
            position: relative;
            height: 15px;
            width: 15px;
            background-color: white;
            justify-content: center;
            border-radius: 50%;
            padding: 15px;

            .icon_plus {
              height: 15px;
              width: 15px;
              position: absolute;
              top: 52%;
              left: 58%;
              transform: translate(-50%, -50%);
              color: #71abff;
            }
          }
        }
      }
    }
    .line-header {
      margin-top: 0.5rem;
    }
    .caterory-products {
      .title-caterory-products {
        display: flex;
        justify-content: space-between;
        padding: 15px 15px;
        position: relative;
        border-bottom: 1px solid #e6e6e8;
        .item-title-caterory {
          color: #999999;
        }
        .item-title-caterory.address-store {
          position: absolute;
          left: 20%;
        }
        .item-title-caterory.city-store {
          position: absolute;
          left: 42%;
        }
        .item-title-caterory.phone-store {
          position: absolute;
          left: 56%;
        }
        .item-title-caterory.number-employee {
          position: absolute;
          left: 68%;
        }
        .item-title-caterory.statusproduct {
          position: absolute;
          left: 80%;
        }
      }
    }
    .list-category {
      .box-category {
        .content-box-category {
          display: flex;
          position: relative;
          justify-content: space-between;
          padding: 5px 15px;
          border-bottom: 1px solid #e6e6e8;
          .item-detail-caterory {
            padding: 8px 0;
            line-height: 34px;
            .icon {
              padding: 5px;
            }
          }
          .item-detail-caterory.address-store {
            position: absolute;
            width: 250px;
            height: 40px;
            left: 20%;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          .item-detail-caterory.city-store {
            position: absolute;

            left: 42%;
          }

          .item-detail-caterory.phone-store {
            position: absolute;
            left: 56%;
          }
          .item-detail-caterory.number-employee {
            position: absolute;
            left: 70%;
          }
          .item-detail-caterory.statusproduct {
            position: absolute;
            left: 80%;
            color: #29d3aa;
          }
        }
      }
    }
  }
}
