.detail-order {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;

  .sub-menu ul li a {
    font-size: 13px;
  }
  .content-detail-order {
    padding: 0px 15px 15px 15px;
    // height: 90vh;
    .order {
      .titile-order {
        padding-top: 15px;
        span {
          vertical-align: sub;
          font-weight: bold;
          font-size: 1.2rem;
        }
      }
      .list-product-order {
        padding: 15px;
        .product {
          padding: 10px 0;
          display: flex;
          justify-content: flex-start;
          position: relative;
          border-bottom: 1px solid #e9e8e8;
          .btn-del {
            padding-right: 0px;
            position: relative;
            i {
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
            .fa-times-circle:before {
              color: #f25366;
              font-size: 20px;
            }
          }
          .viewImgProduct {
            padding-left: 0px;
            position: relative;
            width: auto;
            .img-product {
              width: 70px;
            }
          }
          .view-name-price {
            position: relative;
            .name-price {
              margin-top: 12px;
              .name-product {
                font-weight: bold;
              }
              .price-product {
                color: #0439d8;
                font-weight: bold;
              }
            }
          }

          .view-btn-add {
            position: relative;
            // padding-right: 0px;
            .btn-add {
              position: absolute;
              right: -20px;
              top: 50%;
              transform: translate(-50%, -50%);
              .view-btn {
                position: relative;
                width: 20px;
              }
              .amount-product {
                height: 40px;
                padding: 0 20px;
              }
              .fa.fa-plus,
              .fa.fa-minus {
                position: relative;
                height: 20px;
                width: 20px;
                color: #71abff;
                justify-content: center;
                border-radius: 50%;
                padding: 15px;
                border: 1px solid #71abff;
              }
              .fa-plus::before,
              .fa-minus:before {
                position: absolute;
                top: 52%;
                left: 50%;
                transform: translate(-50%, -50%);
              }
              .fa.fa-minus {
                margin-right: 30px;
                color: #e0e0e0;
                border: 1px solid #e0e0e0;
              }
              button {
                width: 33px;
                position: absolute;
                top: 45%;
                left: 50%;
                transform: translate(-50%, -50%);
                padding: 0;
              }
            }
          }
        }
      }
      .add-product {
        margin-top: 30px;
        border: 1px solid #71abff;
        padding: 12px 30px;
        border-radius: 15px;
        color: #71abff;
        font-size: 1.2rem;
        font-weight: bold;
      }
    }
    .right-order {
      background-color: #f9f9f9;
      padding-top: 15px;
      position: relative;
      height: 78vh;
      .number-provisional {
        margin-top: 10px;
        display: flex;
        justify-content: space-between;
        .title-provisional {
          font-size: 0.9rem;
          color: #999999;
        }
        .provisional-amount {
          // font-size: 1.1rem;
          color: black;
        }
        .MuiFormControlLabel-root {
          margin-left: 0px;
          margin-right: 0px;
          margin-bottom: 0px;
          .MuiSwitch-root {
            margin: 0px;
          }
        }
      }
      .vocher {
        .box-vocher {
          margin-top: 15px;
          border: 1px solid #e5e5e5;
          background-color: white;
          border-radius: 8px;
          .form-check {
            padding-left: 0px;
            [type='radio']:checked,
            [type='radio']:not(:checked) {
              position: absolute;
              left: -9999px;
            }
            [type='radio']:checked + label,
            [type='radio']:not(:checked) + label {
              position: relative;
              padding-left: 28px;
              cursor: pointer;
              line-height: 20px;
              display: inline-block;
              color: #666;
            }
            [type='radio']:checked + label:before,
            [type='radio']:not(:checked) + label:before {
              content: '';
              position: absolute;
              left: 0;
              top: 0;
              width: 18px;
              height: 18px;
              border: 1px solid #ddd;
              border-radius: 100%;
              background: #fff;
            }
            [type='radio']:checked + label:after,
            [type='radio']:not(:checked) + label:after {
              content: '';
              width: 12px;
              height: 12px;
              background: #407bfd;
              position: absolute;
              top: 3px;
              left: 3px;
              border-radius: 100%;
              -webkit-transition: all 0.2s ease;
              transition: all 0.2s ease;
            }
            [type='radio']:not(:checked) + label:after {
              opacity: 0;
              -webkit-transform: scale(0);
              transform: scale(0);
            }
            [type='radio']:checked + label:after {
              opacity: 1;
              -webkit-transform: scale(1);
              transform: scale(1);
            }
            .item-check {
              border-bottom: 1px solid #e5e5e5;
              label {
                margin-bottom: 0px;
              }
              .content-item-check {
                padding: 10px;
              }
            }
            .item-check:last-child {
              border-bottom: none;
            }
          }
        }
      }
      .box-total-amount {
        margin-top: 15px;
        padding: 15px;
        border: 1px solid #e5e5e5;
        border-radius: 8px;
        text-align: center;
        .title-box-total-amount {
          // font-size: 0.9rem;
          color: #999999;
        }
        .number-amount {
          font-size: 1.8rem;
          color: #f25366;
          font-weight: bold;
        }
      }
      .btn-pay {
        position: absolute;
        top: 60%;
        width: 90%;
        padding-left: 0px;
        .item-btn {
          list-style-type: none;
          margin-bottom: 15px;
          button {
            padding: 12px;
            border: none;
            width: 100%;
            border-radius: 8px;
            color: white;
            font-weight: bold;
            background: linear-gradient(to right, #70a9ff, #1b47ce);
            background: -moz-linear-gradient(right, #70a9ff, #1b47ce);
            background: -webkit-linear-gradient(right, #70a9ff, #1b47ce);
            background: -o-linear-gradient(right, #70a9ff, #1b47ce);
            img {
              height: 25px;
              padding-right: 10px;
            }
          }
        }
        .item-btn:last-child {
          button {
            border: 1px solid #b8d5ff;
            border-radius: 8px;
            background: linear-gradient(to right, #fff, #fff);
            background: -moz-linear-gradient(right, #fff, #fff);
            background: -webkit-linear-gradient(right, #fff, #fff);
            background: -o-linear-gradient(right, #fff, #fff);
            color: #f25366;
          }
        }
      }
    }
  }
}
