.form_booking {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_air_ticket {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .content_form {
    position: relative;
    height: 100vh;

    .MuiButtonGroup-fullWidth {
      position: absolute;
      left: 50%;
      top: 5%;
      width: auto;
      transform: translate(-50%, -50%);
    }
    .MuiButtonBase-root {
      width: max-content;
      border: 1px solid #407bfd;
      color: #407bfd;
      text-transform: unset;
      font-size: 1rem;
    }
    .MuiButtonBase-root.active {
      background-color: #407bfd;
      color: white;
      font-weight: bold;
      text-decoration: none;
    }
    form {
      top: 10%;
      padding-top: 7%;
      padding: 7% 29%;
      .box-input {
        margin-bottom: 20px;
        .card-cover {
          border: 2px solid #e2e2e2;
          border-radius: 8px;
          .field {
            padding-left: 12px !important;
          }
        }
      }

      .MuiFormControl-root {
        padding: 0px 12px;
      }
      .MuiInput-underline:before {
        border-bottom: none;
      }
      label + .MuiInput-formControl {
        margin-top: 10px;
      }
      .MuiInputLabel-formControl {
        left: 12px;
        top: -10px;
        padding: 0 5px;
        background-color: white;
      }
      .MuiSelect-icon {
        top: calc(50% - 16px);
      }
      .DateRangePicker {
        width: 100%;
        display: unset;
        .DateRangePickerInput {
          position: relative;
          border: none;
          width: 100%;
          display: flex;
          justify-content: space-between;
          .DateInput {
            width: unset;
            border: 1px solid #dbdbdb;
            border-radius: 8px;
          }
          .DateRangePickerInput_arrow {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
          .DateInput_input {
            width: 95%;
            margin: 0 4px;
          }
          .CalendarMonth_caption {
            text-transform: uppercase;
          }
          .CalendarDay__selected {
            background-color: #407afc;
          }
          .CalendarDay__selected_span {
            background-color: #d4e5ff;
            color: #407afc;
          }
        }
      }
      .amount_person {
        padding: 11px 11px 9px;
        position: relative;
        margin-top: 20px;
        margin-bottom: 20px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        border: 2px solid #e2e2e2;
        border-radius: 8px;
        .fa.fa-caret-down {
          position: absolute;
          top: 50%;
          right: 0px;
          transform: translate(-50%, -50%);
        }
      }
      .btn_add {
        margin-right: 15px;
        width: 100%;
        padding: 12px;
        margin-top: 20px;
        color: white;
        font-size: 14px;
        font-weight: bold;
        border-radius: 8px;
        background: linear-gradient(to right, #70a9ff, #1b22a1);
        background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
        background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
        background: -o-linear-gradient(right, #70a9ff, #1b22a1);
      }
    }
  }
}
