.bolder-custom-link {
  font-weight: bolder;
  color: #474749;
}

.border-bottom-custom-link {
  border-bottom: 2px solid #aaaaaa;
  font-weight: bolder;
  padding-bottom: 15px;
  color: #474749;
}

.border-bottom-custom-link-5px {
  border-bottom: 5px solid white;
  padding-bottom: 10px;
}
.logo-left {
  margin-right: 8px;
  width: 30px;
  background-repeat: no-repeat;
  background-position: top;
}
.title-link {
  padding-top: 28px;
  text-align: center;
}
.logo-top {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-right: 8px;
  width: 30px;
  background-repeat: no-repeat;
  background-position: top;
}

@media (max-width: 765px) {
  .step-change-reservation a {
    font-size: 10px;
  }
}
