.create_order_success {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  position: relative;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .view_create_order {
    position: relative;
    height: 70vh;
    .content_create_order {
      background-color: white;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
      .logo-success {
        height: 40px;
      }
      p {
        margin-top: 0.5rem;
        font-weight: bold;
      }
      .box-info-order {
        min-width: 400px;
        border: 2px solid #e5e5e5;
        border-radius: 8px;
        margin-bottom: 30px;
        .row-box-info-order {
          padding: 8px 15px;
          border-bottom: 1px solid #e5e5e5;
          display: flex;
          justify-content: space-between;
          .item-left {
            color: #666666;
          }
        }
        .row-box-info-order:last-child {
          border-bottom: none;
          .item-right {
            font-size: 1.2rem;
            color: #f25366;
          }
        }
      }
      .back-home {
        padding: 12px 80px;
        border-radius: 8px;
        background: linear-gradient(to right, #70a9ff, #1b209f);
        background: -moz-linear-gradient(right, #70a9ff, #1b209f);
        background: -webkit-linear-gradient(right, #70a9ff, #1b209f);
        background: -o-linear-gradient(right, #70a9ff, #1b209f);
        text-transform: uppercase;
        font-weight: bold;
        color: white;
      }
      .group-btn {
        .back-action,
        .support-action {
          margin-right: 20px;
          padding: 6px 25px;
          border-radius: 8px;
          border: 1px solid #407bfd;
        }
        .support-action {
          .fa.fa-headset {
            font-size: 13px;
            color: #007bff;
          }
        }
      }
    }
  }
}
