.info-bill-electric {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_bill_electric {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .box_info_bill {
    padding: 5% 10%;
    .box_info_customer {
      p {
        font-weight: bold;
      }
      .title_info_customer {
        p {
          font-weight: bold;
        }
        .item_info_customer {
          display: flex;
          justify-content: space-between;
          .item_info_customer_left {
            padding: 8px 0px;
            color: #666666;
          }
        }
      }
      .list_bill {
        .item_bill {
          padding: 5px 0px;
          .content_item_bill {
            display: flex;
            justify-content: space-between;
            .item-checkAll .action-selectAll .checkAll {
              top: 3px;
            }
          }
        }
      }
      .list_prepay {
        display: flex;
        justify-content: space-between;
        .item_bill_prepay {
          text-align: center;
          border: 3px solid #a3c3ff;
          border-radius: 8px;
          .title_item_bill_prepay {
            padding: 5px;
            font-size: 1.2rem;
            background-color: #a3c3ff;
            color: white;
          }
          .content_item_bill_prepay {
            padding: 5px 40px;
            .price_item_bill_prepay {
              font-size: 1.3rem;
              font-weight: bold;
            }
          }
        }
        .item_bill_prepay {
          text-decoration: none;
          .time_item_bill_prepay {
            color: #999;
          }
        }
        .item_bill_prepay:hover,
        .item_bill_prepay.active {
          border: 3px solid #3478f6;
          cursor: pointer;
          .title_item_bill_prepay {
            background-color: #3478f6;
          }
        }
      }
      .fees {
        padding: 6px 0px;
        display: flex;
        justify-content: space-between;
        .fees_left {
          color: #666;
        }
      }
    }
  }
  .group-btn-pop-up {
    padding: 15px 10%;
    text-align: center;
    .btn-pop-up {
      margin-right: 15px;
      width: 150px;
      padding: 12px;
      margin-top: 20px;
      color: white;
      font-size: 14px;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #70a9ff, #1b22a1);
      background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
      background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
      background: -o-linear-gradient(right, #70a9ff, #1b22a1);
    }
    .btn-pop-up.come-back {
      color: #777777;
      background: linear-gradient(to right, #e6e6e6, #e6e6e6);
      background: -moz-linear-gradient(right, #e6e6e6, #e6e6e6);
      background: -webkit-linear-gradient(right, #e6e6e6, #e6e6e6);
      background: -o-linear-gradient(right, #e6e6e6, #e6e6e6);
      border: none;
    }
  }
}
