.update_profile {
  background-color: white;
  margin-top: 20px;
  .header_update_profile {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .titile-info-company,
  .titile-info-profile {
    padding-left: 50px;
    position: relative;
    font-size: 1.1rem;
    font-weight: bold;
    width: 100%;
    .hr-info-company {
      position: absolute;
      top: -2px;
      left: 20%;
      width: 80%;
      height: 2px;
    }
  }
  .titile-info-profile {
    .hr-info-company {
      left: 16.5%;
      width: 83.5%;
    }
  }
  .row-first {
    padding: 15px 6%;
    display: flex;
    justify-content: space-between;
    .box-input {
      width: 48%;
    }
  }
  .box-input.number_phone_profile {
    padding-bottom: 30px;
    width: 48%;
    padding-left: 6%;
    .field {
      color: #ccc;
    }
  }
  .box-info-company {
    display: flex;
    justify-content: space-between;
    padding: 15px 10%;
    .box-left {
      width: 48%;
      .box-input {
        position: relative;
        padding-top: 25px;
        .title-product-code {
          color: #666666;
          padding-bottom: 10px;
        }
        .MuiFormControl-marginNormal {
          margin-top: 0px;
          width: 100%;
        }
        .MuiInput-underline:before {
          border-bottom: none;
        }
        .MuiFormControl-root.MuiTextField-root {
          margin: 0px;
          padding: 6px 12px;
        }
        .err-caterory {
          position: absolute;
          bottom: -25px;
          left: 0px;
          width: max-content;
          color: red;
        }
      }
      .condition {
        padding-top: 25px;
        display: flex;
        justify-content: space-between;
        span.title {
          vertical-align: sub;
          color: #666666;
        }
        .MuiFormControlLabel-root {
          margin: 0px;
          .MuiSwitch-root {
            margin: 0px;
          }
        }
      }
      .box_address {
        display: flex;
        justify-content: space-between;
        .item_address {
          width: 48%;
        }
      }
    }
    .box-right {
      position: relative !important;
      margin-top: 50px;
      width: 450px;
      height: 450px;
      background-color: #f8f8f8;
      border-radius: 8px;
      text-align: center;
      align-items: center;
      .content-box-right {
        width: 100%;
        height: 100%;
        position: relative;
        p {
          margin-top: 50px;
          font-weight: bold;
        }
        .box-image {
          overflow: hidden;
          position: absolute;
          top: 30%;
          right: 3%;
          width: max-content;
          height: 48%;
          border-radius: 8px;
          transform: translate(-50%, -50%);
          .none_image {
            width: 50%;
          }
          .notification-noimg {
            color: #666;
          }
          .media-photo.media-round {
            width: 210px;
            height: 210px;
            position: relative;
            #myimage {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              object-fit: cover;
              background-size: cover;
            }
          }
        }
        button {
          padding: 10px 30px;
          position: absolute;
          top: 62%;
          border: 2px solid #407bfd;
          border-radius: 8px;
          color: #407bfd;
          text-transform: uppercase;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
  .card-cover {
    border: 2px solid #e2e2e2;
    border-radius: 8px;
    .field {
      padding-left: 12px !important;
    }
  }
  .group-btn-pop-up {
    padding: 15px 10%;
    .btn-pop-up {
      margin-right: 15px;
      width: 150px;
      padding: 12px;
      margin-top: 20px;
      color: white;
      font-size: 14px;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #70a9ff, #1b22a1);
      background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
      background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
      background: -o-linear-gradient(right, #70a9ff, #1b22a1);
    }
    .btn-pop-up:last-child {
      color: #777777;
      background: linear-gradient(to right, #fff, #fff);
      background: -moz-linear-gradient(right, #fff, #fff);
      background: -webkit-linear-gradient(right, #fff, #fff);
      background: -o-linear-gradient(right, #fff, #fff);
    }
  }
}
