.qr-code {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header-qrcode {
    border-bottom: 1px solid #e9e8e8;
    padding: 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .content-qr-code {
    position: relative;
    width: 100%;
    height: 60vh;
    background-color: white;

    .info-qr-code {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      align-items: center;
      .info-qr-code-content {
        display: flex;
        justify-content: space-between;

        .my-div-qrcode {
          // min-height: 200px;
          box-shadow: 3px 3px 3px 3px rgba(235, 236, 237, 0.6);
          background-color: white;
          z-index: 999;
        }

        .my-qrcode {
          margin: 10px 10px;
          min-width: 200px;
          min-height: 200px;
        }
        .profile-info {
          position: relative;
          min-width: 400px;
          margin: 10px 0px;
          background-color: #f3faff;
          border-top-right-radius: 8px;
          border-bottom-right-radius: 8px;
          img {
            position: absolute;
            top: 30%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 50px;
          }
        }
        .name-merchant {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: fit-content;
          font-weight: bold;
          font-size: 1.1rem;
        }
        .address-merchant {
          position: absolute;
          top: 60%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: fit-content;
          // font-weight: bold;
          font-size: 0.9rem;
          color: #666666;
        }
        .number-phone-merchant {
          padding: 2px 20px;
          position: absolute;
          top: 75%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: fit-content;
          color: #71abff;
          border: 1px solid #71abff;
          border-radius: 10px;
          background-color: white;
        }
      }
    }
  }
}
