.popup_notification {
  position: relative;
  text-align: center;
  min-width: 450px;
  border-radius: 25px;
  .titie_noti {
    padding: 10px;
    border-bottom: 2px solid #e5e5e5;
    font-size: 1.2rem;
    font-weight: bold;
  }
  .off_box {
    height: 100%;
    img {
      position: absolute;
      top: 0;
      right: 0px;
      height: 30px;
    }
  }
  .content_popup {
    padding: 70px;
    font-size: 1.2rem;
    color: #999999;
  }
}
