.sub-menu {
  background-color: #f0f2ff;
  width: 100%;
  color: white;
  font-size: 16px;
}

.sub-menu ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

.sub-menu ul li {
  justify-content: center;
  // padding: 0px 40px;
}

.sub-menu ul li a {
  color: #39394d;
  padding-top: 45px;
  text-decoration: none;
}
.border-bottom-custom-link-5px {
  background-color: white;
  position: relative;
  height: 80px;
}
.nav-menu {
  position: relative;
}

@media (max-width: 991px) {
  .sub-menu a:hover {
    border-bottom: none;
  }
  .dashboard-right div {
    padding-right: 0px;
  }
}

@media (max-width: 768px) {
  .sub-menu ul li {
    display: block;
    width: max-content;
    padding-bottom: 10px;
  }
}

@media (max-width: 1024px) {
  .sub-menu ul li {
    display: inline-flex;
    width: max-content;
    padding: 0 23px !important;
  }

  .sub-menu ul li a {
    width: max-content !important;
  }
}
