.ticket_purchase_history {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_bill_electric {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .contain_ticket_purchase_history {
    padding: 15px;
    .info_customer {
      display: flex;
      justify-content: space-between;
      border: 1px solid #e2e2e2;
      padding: 10px;
      border-radius: 15px;
      .item_info_customer_left {
        position: relative;
        display: flex;
        justify-content: flex-start;
        width: 50%;
        .icon_profile {
          height: 60px;
        }
        .info {
          position: absolute;
          left: 32%;
          top: 46%;
          transform: translate(-50%, -50%);
          .profile_name {
            margin-bottom: 0px;
            font-weight: bold;
            text-transform: uppercase;
            font-size: 1.2rem;
          }
          .profile_email {
            color: #999999;
          }
        }
      }
      .item_info_customer_right {
        text-align: right;
        p {
          margin-top: 0.5rem;
          margin-bottom: 0px;
          color: #999;
        }
        .total_money {
          font-size: 1.4rem;
          color: #0439d8;
          font-weight: bold;
        }
      }
    }
  }
  .list_booking_history {
    padding: 0 15px;
    height: 80vh;
    position: relative;
    .title_list_booking_history,
    .detail-litst-order {
      display: flex;
      justify-content: space-between;
      color: #999999;
    }
    .title_list_booking_history {
      position: relative;
      border-bottom: 2px solid #e5e5e8;
      .item-title {
        text-align: center;
      }
    }
    .detail-litst-order {
      display: flex;
      position: relative;
      justify-content: space-between;
      padding: 15px 0px;
      border-bottom: 1px solid #e6e6e8;
      .item-history {
        color: black;
      }
      .item-history.price {
        position: absolute;
        left: 63%;
        font-weight: normal;
        color: black;
        // transform: translate(-50%, -50%);
      }
      .item-history.time_booking {
        position: absolute;
        left: 30%;
        font-weight: normal;
        color: black;
      }

      .item-history:last-child {
        color: #407afc;
        font-weight: normal;
      }
    }
    .pagination {
      position: absolute;
      left: 50%;
      margin-top: 30px;
      transform: translate(-50%, -50%);
      li {
        position: relative;
        height: 20px;
        width: 20px;
        // background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        margin: 0 5px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: #407afc;
          text-align: center;
          text-decoration: none;
          transform: translate(-50%, -50%);
        }
      }
      li:first-child,
      li:last-child {
        position: relative;
        height: 20px;
        width: 20px;
        background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: white;
          text-align: center;
          transform: translate(-50%, -50%);
        }
      }
      li:last-child {
        a {
          left: 55%;
        }
      }
      li.disabled {
        background-color: #d9e4ed;
      }
      li.active {
        position: relative;
        height: 20px;
        width: 20px;
        justify-content: center;
        border-radius: 50%;
        border: 1px solid #407afc;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}
@media (max-width: 1024px) {
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_left {
    width: 64%;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_left
    .info {
    top: 50%;
    left: 34%;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_left
    .icon_profile {
    height: 50px;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_left
    .info
    .profile_name {
    font-size: 0.9rem;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_left
    .info
    .profile_email {
    font-size: 0.7rem;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_right
    p {
    font-size: 0.7rem;
    margin-top: 0.3rem;
  }
  .ticket_purchase_history
    .contain_ticket_purchase_history
    .info_customer
    .item_info_customer_right
    .total_money {
    font-size: 0.9rem;
  }
}
