.add-employee {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header-add-employee {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .box-add-product {
    display: flex;
    justify-content: space-between;
    padding: 15px 10%;
    .box-left {
      width: 48%;
      .box-input {
        position: relative;
        padding-top: 25px;
        .title-product-code {
          color: #666666;
          padding-bottom: 10px;
        }
      }
    }
    .box-right {
      width: 48%;
      .box-input {
        position: relative;
        padding-top: 25px;
        .title-product-code {
          color: #666666;
          padding-bottom: 10px;
        }
      }
      .condition {
        padding-top: 73px;
        display: flex;
        justify-content: space-between;
        span.title {
          vertical-align: sub;
          color: #666666;
        }
      }
      .MuiFormControlLabel-root {
        margin: 0px;
        .MuiSwitch-root {
          margin: 0px;
        }
      }
      .MuiFormControl-marginNormal {
        margin-top: 0px;
        width: 100% !important;
      }
      .MuiInput-underline:before {
        border-bottom: none !important;
      }
      .MuiFormControl-root.MuiTextField-root {
        margin: 0px;
        padding: 6px 12px;
      }
      .err-caterory {
        position: absolute;
        bottom: -25px;
        left: 0px;
        width: max-content;
        color: red;
      }
    }
  }
  .card-cover {
    border: 2px solid #e2e2e2;
    border-radius: 8px;
    .field {
      padding-left: 12px !important;
    }
  }
  .group-btn-pop-up {
    padding: 15px 10%;
    .btn-pop-up {
      margin-right: 15px;
      width: 150px;
      padding: 12px;
      margin-top: 20px;
      color: white;
      font-size: 14px;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #70a9ff, #1b22a1);
      background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
      background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
      background: -o-linear-gradient(right, #70a9ff, #1b22a1);
    }
    .btn-pop-up:last-child {
      color: #777777;
      background: linear-gradient(to right, #fff, #fff);
      background: -moz-linear-gradient(right, #fff, #fff);
      background: -webkit-linear-gradient(right, #fff, #fff);
      background: -o-linear-gradient(right, #fff, #fff);
    }
  }
}
