.choose_employee {
  min-width: 500px;
  min-height: 200px;
  position: relative;
  text-align: center;
  .viewClose {
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 90000000;
  }
  .icon-close {
    position: relative;
    height: 20px;
    width: 20px;
    // border: 1px solid #71abff;
    justify-content: center;
    border-radius: 50%;
    padding: 15px;
    background-color: #f25366;
    img {
      height: 20px;
      width: 20px;
      position: absolute;
      top: 50%;
      left: 51%;
      transform: translate(-50%, -50%);
    }
  }
  .title_add_employee {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin: 0rem;
  }
  .list_employee {
    .item_employee {
      border-bottom: 1px solid #e5e5e5;
      .content_item_employee {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 10px 15px;
        span {
          font-weight: 400;
        }
        .role_employee {
          position: absolute;
          right: 60px;
          top: 8px;
          color: #999;
          font-weight: normal;
        }
      }
    }
  }
  .btn_choose_employee {
    padding: 10px 20px;
    width: 40%;
    margin: 20px;
    border: 1px solid #407bfd;
    color: #407bfd;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.2rem;
  }
}
