.list_search_tution {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_tuition {
    padding: 15px 15px;
    span {
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .title-litst-order {
    position: relative;
    border-bottom: 2px solid #e5e5e8;
    .item-title {
      text-align: center;
    }
  }
  .detail-litst-order {
    display: flex;
    position: relative;
    justify-content: space-between;
    padding: 15px 0px;
    border-bottom: 1px solid #e6e6e8;
    .item_search {
      font-weight: normal;
      text-transform: capitalize;
    }
    .item_search.nameSchool {
      position: absolute;
      left: 23%;
      font-weight: normal;
      color: black;
      // transform: translate(-50%, -50%);
    }
    .item_search.class {
      position: absolute;
      left: 45%;
      font-weight: normal;
      color: black;
    }
    .item_search.birth {
      position: absolute;
      left: 67%;

      font-weight: normal;
    }

    .item_search:last-child {
      color: #407afc;
      font-weight: normal;
    }
  }
}
