.add-caterory {
  min-width: 700px;
  .titie-add-caterory {
    // padding: 15px;
    border-bottom: 1px solid #cccccc;
    .content-titie-add-caterory {
      display: flex;
      justify-content: space-between;
      padding: 15px;
      .titie-add-caterory-left {
        span {
          font-size: 1.1rem;
          font-weight: bold;
          color: #39394d;
        }
      }
      .titie-add-caterory-right {
        img {
          width: 20px;
          color: yellow;
          fill: yellow;
        }
      }
    }
  }
  .box-add-caterory {
    padding: 15px;
    .name-code-caterory {
      display: flex;
      justify-content: space-between;
      .namecaterory,
      .catalogCode {
        width: 49%;
      }
    }
    .title-name-caterory,
    .title-catalog-code,
    .title-catalog-description {
      color: #666666;
      padding-bottom: 10px;
    }
    .descriptionCaterory {
      padding-top: 20px;
    }
  }
  .card-cover {
    border: 2px solid #e2e2e2;
    border-radius: 8px;
    .field {
      padding-left: 12px !important;
    }
  }
  .group-btn-pop-up {
    padding: 15px;
    .btn-pop-up {
      margin-right: 15px;
      width: 150px;
      padding: 12px;
      margin-top: 20px;
      color: white;
      font-size: 14px;
      font-weight: bold;
      border-radius: 8px;
      background: linear-gradient(to right, #70a9ff, #1b22a1);
      background: -moz-linear-gradient(right, #70a9ff, #1b22a1);
      background: -webkit-linear-gradient(right, #70a9ff, #1b22a1);
      background: -o-linear-gradient(right, #70a9ff, #1b22a1);
    }
    .btn-pop-up:last-child {
      color: #777777;
      background: linear-gradient(to right, #fff, #fff);
      background: -moz-linear-gradient(right, #fff, #fff);
      background: -webkit-linear-gradient(right, #fff, #fff);
      background: -o-linear-gradient(right, #fff, #fff);
    }
  }
}
