.booking_history {
  background-color: white;
  padding-bottom: 25px;
  margin-top: 20px;
  .sub-menu ul li {
    padding: 0 40px;
  }
  .sub-menu ul li a {
    font-size: 13px;
  }
  .header_booking_history {
    padding: 15px 15px;
    display: flex;
    justify-content: space-between;
    height: 70px;
    position: relative;
    span {
      font-weight: bold;
      font-size: 1.2rem;
      position: absolute;
      top: 50%;
      left: 7%;
      transform: translate(-50%, -50%);
    }
    .box-search {
      position: absolute;
      top: 50%;
      right: -10%;
      transform: translate(-50%, -50%);
      border: 1px solid #e5e5e5;
      border-radius: 30px;
      .input {
        .img-email {
          left: 8%;
        }
        .field {
          width: 250px;
          padding-left: 38px;
        }
      }
    }
  }
  .line-header {
    margin-top: 0rem;
  }
  .list_booking_history {
    padding: 0 15px;
    height: 80vh;
    position: relative;
    .title_list_booking_history,
    .detail-litst-order {
      display: flex;
      justify-content: space-between;
      color: #999999;
    }
    .title_list_booking_history {
      position: relative;
      border-bottom: 2px solid #e5e5e8;
      .item-title {
        text-align: center;
      }
    }
    .detail-litst-order {
      display: flex;
      position: relative;
      justify-content: space-between;
      padding: 15px 0px;
      border-bottom: 1px solid #e6e6e8;
      .item-history {
        color: black;
      }
      .item-history.phone_number {
        position: absolute;
        left: 30%;
        font-weight: normal;
        color: black;
        // transform: translate(-50%, -50%);
      }
      .item-history.time_booking {
        position: absolute;
        left: 60%;
        font-weight: normal;
        color: black;
      }

      .item-history:last-child {
        color: #407afc;
        font-weight: normal;
      }
    }
    .pagination {
      position: absolute;
      left: 50%;
      margin-top: 30px;
      transform: translate(-50%, -50%);
      li {
        position: relative;
        height: 20px;
        width: 20px;
        // background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        margin: 0 5px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: #407afc;
          text-align: center;
          text-decoration: none;
          transform: translate(-50%, -50%);
        }
      }
      li:first-child,
      li:last-child {
        position: relative;
        height: 20px;
        width: 20px;
        background-color: #407afc;
        justify-content: center;
        border-radius: 50%;
        padding: 15px;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          color: white;
          text-align: center;
          transform: translate(-50%, -50%);
        }
      }
      li:last-child {
        a {
          left: 55%;
        }
      }
      li.disabled {
        background-color: #d9e4ed;
      }
      li.active {
        position: relative;
        height: 20px;
        width: 20px;
        justify-content: center;
        border-radius: 50%;
        border: 1px solid #407afc;
        a {
          height: 20px;
          width: 20px;
          position: absolute;
          top: 42%;
          left: 51%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}
@media (max-width: 1024px) {
  .booking_history .header_booking_history span {
    left: 11%;
  }
  .booking_history .header_booking_history .box-search {
    right: -15%;
  }
}
