.from_info_student {
  opacity: 1;
  transition: opacity 500ms ease-in;
  transition: all 1s ease;
  .box-input {
    position: relative;
    .title-product-code {
      color: #666666;
      padding-bottom: 10px;
    }
  }
  .card-cover {
    border: 2px solid #e2e2e2;
    border-radius: 8px;
    margin-top: 0px !important;
    margin-bottom: 20px !important;
    .field {
      padding-left: 12px !important;
    }
  }
  .MuiFormControlLabel-root {
    margin: 0px;
    .MuiSwitch-root {
      margin: 0px;
    }
  }
  .MuiFormControl-marginNormal {
    margin-top: 0px;
    width: 100% !important;
  }
  .MuiInput-underline:before {
    border-bottom: none !important;
  }
  .MuiFormControl-root.MuiTextField-root {
    margin: 0px;
    padding: 6px 12px;
  }
  .btn-continue {
    padding: 12px;
    width: 20%;
    color: white;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
    border-radius: 8px;
    background: linear-gradient(to right, #6da7fe, #1b20a0);
    background: -moz-linear-gradient(right, #6da7fe, #1b20a0);
    background: -webkit-linear-gradient(right, #6da7fe, #1b20a0);
    background: -o-linear-gradient(right, #6da7fe, #1b20a0);
  }
}
